@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600;700&display=swap);
.about__container {
  background-image: url(/react-exam/static/media/popcorn.a8da62e9.jpg);
  background-repeat: no-repeat;
  background-position: center;
  height: 87vh;
  text-align: center;
  margin-top: 0;
}

.home__container {
  background-image: url(/react-exam/static/media/chairs.81741049.jpg);
  background-repeat: no-repeat;
  background-position: center;
  height: 87vh;
  text-align: center;
  margin-top: 0;
}

.home__container h1 {
  margin-top: 0;
  padding: 2em;
}
.card__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 3em;
  gap: 3em;
}


.movieView__container {
}

.movie__container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: 1fr, 2fr, 2fr;
  border: solid 4px #103c61;
  border-radius: 30px;
  grid-gap: 2em;
  gap: 2em;
  width: 55vw;
  margin: auto;
  margin-top: 2em;
}

.movie__title {
  text-align: center;
  grid-column-start: 3;
  grid-column-end: 6;
}
.movie__card {
  grid-column: 3;
  grid-row: 3;
  text-align: center;
  justify-self: end;
  margin-right: 3em;
}
.release__date {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row: 4;
  justify-self: center;
}
.overview {
  grid-column-start: 4;
  grid-column-end: 7;
  grid-row: 3;
  align-self: center;
  justify-self: center;
  margin-right: 3em;
}

.voting {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row: 4;
  text-align: center;
  padding: 2em;
}
.movie_backdrop {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row: 2;
  justify-self: center;
  align-self: center;
  width: 765px;
  object-fit: cover;
}

.navDesk__container {
  background-color: #852024;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  justify-content: flex-end;
  place-items: center;
}
.navDesk__container button {
  width: 6em;
  margin-right: 0.5em;
  font-size: 2rem;
  height: 2.5em;
  color: #cfb584;
  background-color: #852024;
  border: none;
  border-radius: 15px;
}
.navDesk__container button:hover {
  background-color: #103c61;
  cursor: pointer;
}
.btn--home {
  grid-column-start: 9;
}
.btn--about {
  grid-column-start: 10;
}
.logo {
  grid-column-start: 1;
  margin-left: 1.5em;
  width: 10em;
  cursor: pointer;
}
.logo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

body {
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif;
  background-color: #212121;
  color: whitesmoke;
}

